import * as timeDifference from 'time-difference-js'

export const dateFormat = (date) => {
  const now = new Date(date)

  let year = now.getFullYear()// 
  if (year < 1900) year = year + 1900

  let month = now.getMonth() + 1// 
  if (month < 10) month = '0' + month

  let day = now.getDate()// 
  if (day < 10) day = '0' + day

  return year + '/' + month + '/' + day
}

export const dateDifference = (date) => {
  const { getTimeDiff } = timeDifference
  const startDate = new Date(date)
  const endDate = new Date()

  const result = getTimeDiff(startDate, endDate)
  const { value, suffix } = result

  switch (suffix) {
    case 'second':
    case 'seconds':
      result.text = value + ' '
      break
    case 'minute':
    case 'minutes':
      result.text = value + ' '
      break
    case 'hour':
    case 'hours':
      result.text = value + ' '
      break
    case 'days':
      result.text = value + ' '
      break
    case 'weeks':
      result.text = value + ' '
      break
    case 'months':
      result.text = value + ' '
      break
    case 'year':
      result.text = value + ' '
      break
    case 'years':
      result.text = dateFormat(date)
      break
  }

  return result.text
}
