import axios from 'axios'
import { extend } from 'vee-validate'

import {
  required,
  email,
  min,
  max,
  confirmed,
  alpha_num as alphaNum
} from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: (fieldName) => {
    if (fieldName === 'password') fieldName = ''
    if (fieldName === 'new-password') fieldName = ''
    if (fieldName === 'username') fieldName = ''
    if (fieldName === 'confirmPassword') return ''
    return `${fieldName}`
  }
})

extend('email', {
  ...email,
  message: '{_field_}'
})

extend('min', {
  ...min,
  message: (fieldName, placeholders) => {
    if (fieldName === 'password') fieldName = ''
    if (fieldName === 'new-password') fieldName = ''
    if (fieldName === 'username') fieldName = ''
    return `${fieldName}${placeholders.length}`
  }
})

extend('max', {
  ...max,
  message: (fieldName, placeholders) => {
    if (fieldName === 'password') fieldName = ''
    if (fieldName === 'new-password') fieldName = ''
    if (fieldName === 'username') fieldName = ''
    if (fieldName === 'about') fieldName = ''
    return `${fieldName}${placeholders.length}`
  }
})

extend('confirmed', {
  ...confirmed,
  message: ''
})

extend('alphaNum', {
  ...alphaNum,
  message: ''
})

extend('emailUnique', {
  validate: async (value) => {
    try {
      const res = await axios.get(process.env.VUE_APP_API + '/users/' + value)
      return res.data.success
    } catch (error) {
      return error.response.data.success
    }
  },
  message: 'email '
})

extend('bannedName', {
  validate: (value) => { return !/Cuishare/i.test(value) },
  message: ''
})
